img {
  border-radius: 4px;
}

.site-content {
  column-gap: 20px;
}

#sticky-navigation, #site-navigation {
  padding-inline: 20px;
  border-bottom: 3px solid var(--accent);
}
#sticky-navigation .navigation-branding, #site-navigation .navigation-branding {
  margin-left: 0px;
}

@media (max-width: 1240px) {
  body:not(.full-width-content) .site {
    padding-inline: 20px;
  }
}
/* Hide Submenu-Arrows in Main-Menu */
@media (min-width: 930px) {
  #primary-menu .menu-item-has-children .dropdown-menu-toggle {
    display: none;
  }
  #primary-menu .main-navigation .main-nav ul li.menu-item-has-children a {
    padding-right: 10px;
  }
}
/* PV Calculator */
.hero-width-calculator {
  width: 100%;
  transition: transform 0.2s ease-out;
  display: flex;
}
.hero-width-calculator div:nth-child(2) {
  transition: transform 0.2s ease-out;
  width: 100%;
  flex-shrink: 0;
}
.hero-width-calculator div:nth-child(1) {
  transition: transform 0.2s ease-in;
  width: 100%;
  flex-shrink: 0;
}

.pv-calculator-toggle {
  cursor: pointer;
}

/* Footer */
.site-footer .wp-block-categories-list, .site-footer .wp-block-latest-posts {
  margin-left: 0px;
}
.site-footer .wp-block-categories-list li, .site-footer .wp-block-latest-posts li {
  margin-bottom: 10px;
}
.site-footer .wp-block-categories-list li::marker, .site-footer .wp-block-latest-posts li::marker {
  display: none;
}

.related-posts-title {
  font-weight: 400;
  font-size: 2rem;
  line-height: 1.5em;
}

.related-posts {
  display: flex;
  gap: 10px;
  margin-bottom: 20px !important;
}
@media (max-width: 650px) {
  .related-posts {
    flex-direction: column;
  }
}

.related-post {
  flex: 1;
  gap: 10px;
}
.related-post img {
  width: 100%;
}

.cf7-form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  column-gap: 20px;
  grid-template-areas: "Namee Namee" "Telefon Email" "Time Time";
}
.cf7-form .name {
  grid-area: Namee;
}
.cf7-form .telefon {
  grid-area: Telefon;
}
.cf7-form .email {
  grid-area: Email;
}
.cf7-form .time {
  grid-area: Time;
}
.cf7-form input {
  width: 100%;
}

.frm_forms .frm_image_options {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.frm_forms .frm_image_options .frm_image_option {
  margin: 0px !important;
  flex-shrink: 0;
}
@media (max-width: 550px) {
  .frm_forms .frm_image_options .frm_image_option {
    flex-basis: calc(50% - 10px);
  }
}
.frm_forms .frm_image_options .frm_opt_container {
  margin: 0px;
  justify-content: space-around;
  width: 100%;
}
.frm_forms .frm_image_options .frm_opt_container .frm_image_option_container {
  border: none;
  padding: 10px;
  cursor: pointer;
}
.frm_forms .frm_image_options .frm_opt_container .frm_image_option_container img {
  object-fit: contain;
}

/* custom avatar */
.custom-avatar {
  border-radius: 50%;
  width: 100px;
  height: 100px;
  overflow: hidden;
}
.custom-avatar img {
  width: 100%;
}